import React from "react";
import { Link } from "react-router-dom";

const HomePage = () => {

 
  return (
    <div className="container">
      <div className="flex-container" >
      <h2 className="center">Bilgi Yarmasna Hogeldiniz.</h2>
      </div>
      <div className="home" >
      
      <p className="justify indent">
        Uygulamamz sayesinde hem elenecek, hem de reneceksiniz.
      </p>
      <p className="justify indent">
        Yarma ierisinde 4 seenekli sorular bulunmaktadr ve her sorunun tek
        cevab vardr. Sorular bir soru havuzu ierisinden rastgele
        seilir.Sorular kolaydan zora doru sorulur. lk 5 soru 1 puan, 5'den
        10'a kadar 2 puan, 10'dan 20'ye kadar 3 puan, 20'den 30'a kadar 4
        puan, 30'uncu sorudan itibaren de 5 puandr.
      </p>
      <p className="justify indent">
        Soruyu cevaplamak iin 30 saniyeniz var. Her yarmada 3 joker hakknz
        var. 50% jokeri ile yanl iki k elenir. Soruyu ge jokeri ile bir
        sonraki soruya geebilirsiniz. Sre yetmezse sreyi uzat jokeri ile
        sreyi 30 saniye uzatabilirsiniz.
      </p>
      <p className="justify indent">
        Bir saatlik sre ierinde 3 kere yarma hakknz vardr.Yarmaya
        balamak iin siteye ye olmanz gerekmektedir.
      </p>
      <div className="flex-container">
        <Link to="/exam/all" className="button">
          Yarmaya Bala
        </Link>
        </div>
      </div>
    </div>
  );
};

export default HomePage;
